/* Delikatny szary kolor tła */
body {
    background-color: #f2f2f2;
    margin: 0; /* Usunięcie marginesów */
    padding: 0; /* Usunięcie wcięć */
}

/* Styl dla pola wyszukiwania */
#search-box {
    margin-top: 20px;
    padding: 10px;
    margin-left: 20px;
    background: linear-gradient(to bottom, #f0f0f0, #e0e0e0);
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    color: #333;
}

/* Styl dla listy krajów */
#country-list {
    margin-top: 20px;
    padding: 10px;
    margin-left: 20px;
}

/* Styl dla informacji o kraju */
#country-info {
    margin-top: 20px;
    padding: 10px;
    margin-left: 20px;
}
